<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;div style=\&quot;margin-bottom: 24px\&quot;&gt; {{current}} &lt;/div&gt;\n        &lt;v-steps :current=\&quot;current - 1\&quot;&gt;\n          &lt;v-step :key=\&quot;s.index\&quot; :title=\&quot;s.title\&quot; v-for=\&quot;s in steps\&quot;&gt;&lt;/v-step&gt;\n        &lt;/v-steps&gt;\n        &lt;div style=\&quot;margin-top: 24px\&quot;&gt;\n          &lt;button type=\&quot;button\&quot; name=\&quot;button\&quot; class=\&quot;ant-btn\&quot; @click=\&quot;next\&quot;&gt;&lt;/button&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            current: 1,\n            steps: [\n                {\n                    title: \&quot;1\&quot;,\n                    index: 0\n                },\n                {\n                    title: \&quot;2\&quot;,\n                    index: 1\n                },\n                {\n                    title: \&quot;3\&quot;,\n                    index: 2\n                },\n                {\n                    title: \&quot;4\&quot;,\n                    index: 3\n                }\n            ],\n        }\n    },\n    methods: {\n        next() {\n            if (this.current &lt; this.steps.length) {\n                this.current ++\n            } else {\n                this.current = 1\n            }\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <div style="margin-bottom: 24px"> {{current}} </div>
        <v-steps :current="current - 1">
          <v-step :key="s.index" :title="s.title" v-for="s in steps"></v-step>
        </v-steps>
        <div style="margin-top: 24px">
          <button type="button" name="button" class="ant-btn" @click="next"></button>
        </div>
    </div>
</template>

</div>
