<script>
export default {
    data() {
        return {
            current: 1,
            steps: [
                {
                    title: "1",
                    index: 0
                },
                {
                    title: "2",
                    index: 1
                },
                {
                    title: "3",
                    index: 2
                },
                {
                    title: "4",
                    index: 3
                }
            ],
        }
    },
    methods: {
        next() {
            if (this.current < this.steps.length) {
                this.current ++
            } else {
                this.current = 1
            }
        }
    }
}
</script>


